.more-projects-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh; 
  background: linear-gradient(to bottom right, #1e293b, #0f172a);
  color: white;
  padding: 50px 20px;
  text-align: center;
}
  
  .experience-section h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #ff9900;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    font-family: 'circular-bold', sans-serif;
  }
  
  .projects-container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    padding: 20px;
  }
  
  .project-card {
    display: flex;
    flex-direction: column; 
    align-items: center; 
    background: linear-gradient(135deg, #334155, #64748b);
    color: white;
    padding: 20px;
    border-radius: 15px;
    width: 100%;
    max-width: 600px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.5s ease;
    position: relative;
    overflow: hidden;
  }
  
  .project-card:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
    background: linear-gradient(135deg, #161a20, #333b47);
  }
  
  .project-logo {
    width: 250px;
    height: 250px;
    object-fit: contain;
    margin-bottom: 20px;
    transition: transform 0.3s ease;
  }
  
  .project-logo:hover {
    transform: rotate(5deg) scale(1.1);
  }
  
  .github-link {
    margin-bottom: 15px; 
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    color: white;
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s ease, transform 0.3s ease;
  }
  
  .github-link:hover {
    color: #a78bfa;
    transform: scale(1.1);
  }
  
  .github-icon {
    font-size: 1.5rem;
    transition: transform 0.3s ease;
  }
  
  .github-link:hover .github-icon {
    transform: rotate(360deg);
  }
  
  .project-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .project-details h3 {
    margin: 0 0 5px;
    font-size: 1.8rem;
    color: #a78bfa;
    transition: color 0.3s ease;
  }
  
  .project-details h3:hover {
    color: #9333ea;
  }
  
  .project-details p {
    margin: 10px 0;
    font-size: 1rem;
    color: #e5e7eb;
  }
  
  .tech-stack {
    margin-top: 10px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
  }
  
  .tech-item {
    background-color: #475569;
    color: white;
    font-size: 0.9rem;
    padding: 5px 10px;
    border-radius: 8px;
    text-align: center;
    transition: background-color 0.3s ease;
  }
  
  .tech-item:hover {
    background-color: #a78bfa;
  }
  
  .back-button-container {
    margin-top: 20px;
    text-align: center;
  }
  
  .back-button {
    background: linear-gradient(135deg, #334155, #64748b);
    color: white;
    padding: 12px 24px;
    border-radius: 8px;
    font-size: 1.2rem;
    font-weight: bold;
    text-decoration: none;
    transition: transform 0.3s ease, background 0.5s ease;
    display: inline-block;
  }
  
  .back-button:hover {
    transform: scale(1.05);
    background: linear-gradient(135deg, #161a20, #333b47);
  }
  
  footer {
    margin-top: auto;
  }

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  max-width: 80%;
  max-height: 80%;
  animation: popIn 0.3s ease-out;
}
.modal-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
}

.close-modal {
  position: absolute;
  top: -10px;
  right: -10px;
  background: #ff9900;
  border: none;
  border-radius: 50%;
  font-size: 1.5rem;
  width: 30px;
  height: 30px;
  cursor: pointer;
  color: white;
}

@keyframes popIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
